.item {
  width: calc(33.3333% - 20px);
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: padding-box;
  padding: 50px 30px;
  background-color: #ffffff;
  box-shadow: 0px 10px 30px rgba(219, 219, 219, 0.7);
  border-radius: 30px;
}

.img {
  margin-bottom: 30px;
}

.description {
  text-align: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.05em;
  color: #2d2d2d;
}